import React, { useReducer } from 'react';

import UsersContext from './usersContext';
import UsersReducer from './usersReducer';



import {
  GET_GROUPS,
  GET_GROUP,
  GET_USERS,
  SET_LOADING,
  RESOURCE_URL,
  GET_USER_PROFILE,
  ADD_NEW_GROUP,
  DELETE_USER_PROFILE,
  GROUP_ERROR,
  FILTER_USERS,
  FILTER_TEACHERS,
  CLEAR_FILTER,
  ADD_NEW_USER_TO_GROUP,
  DELETE_USER_FROM_GROUP,
  USERS_ERROR
} from '../types';



const UsersState = props => {
  const session = JSON.parse(localStorage.getItem('session'));
  let headers = { headers : { 'Content-Type': 'application/json; charset=utf-8' }}
  if ( session && session.length > 0 ) {
    
    headers = { headers : { 'Content-Type': 'application/json; charset=utf-8', 'authorization': 'Bearer ' +  session.token }}
  }
  // const postHeaders = { headers : { 'Content-Type': 'application/json; charset=utf-8', 'authorization': 'Bearer ' +  session.token }}
   //Set Loading

  
  //Set State
  const initialState = {
    currentUser : null,
    users: [],
    edituser: false,
    userGroupData : [],
    groups: null,
    loading: false,
    filtered: null,
    groupError: null,
    total : 0,
    currentGroup : null
  }

  // Connect to Reducer
  const [state, dispatch] = useReducer(UsersReducer, initialState);


  // //Get the Current User
  // const getCurrentUser = () => {

  // }

  // Get All Users
  const getAllUsers = async ( limit=100 , offset=0 ) => {
    dispatch({ type : SET_LOADING })
    const session = JSON.parse(localStorage.getItem('session'));
    const path = `${RESOURCE_URL}/wp-json/hot/v2/students/?limit=${limit}&offset=${offset}`;
    headers = { headers: { 'Content-Type': 'application/json; charset=utf-8', 'authorization': 'Bearer ' + session.token } };
   try {
    const results = await fetch(path, headers);
    const jsonRes = await results.json();
    dispatch({
      type: GET_USERS,
      payload: jsonRes
    });
   } catch (error){
      dispatch({
      type: USERS_ERROR,
      payload: error
    });
   }
  }


  // register a new user
  const registerUser = async data => {
    console.log(data);
    const session = JSON.parse(localStorage.getItem('session'));
  
    const path = `${RESOURCE_URL}/wp-json/hot/v2/students/`;
    const results = await fetch(
      path, {
        method       : 'POST',
        headers      : { 'Content-Type': 'application/json; charset=utf-8', 'authorization': 'Bearer ' +  session.token },
        body: JSON.stringify(data)
      }
    );
    await results.json();
    getAllUsers();
  }

  // get the user Profile
  const getUserProfile = async (user) => {
   
    const session = JSON.parse(localStorage.getItem('session'));
    headers = { headers : { 'Content-Type': 'application/json; charset=utf-8', 'authorization': 'Bearer ' +  session.token }}
    const path = `${RESOURCE_URL}/wp-json/hot/v2/groups/users/${ user.id }`;
    const results = await fetch(path, headers)
    const jsonRes = await results.json();
    console.log(jsonRes);
    jsonRes['user'] = user;
    dispatch({
      type: GET_USER_PROFILE,
      payload :  jsonRes
    })
  }

  // remove the userProfile ou of memery
  const deleteUserProfile = () => {
    dispatch({ type: DELETE_USER_PROFILE });
  }


  // Get Groups
  const getGroups = async () => {
    //setLoading();
    const session = JSON.parse(localStorage.getItem('session'));
    if ( session ) {
      headers = { headers : { 'Content-Type': 'application/json; charset=utf-8', 'authorization': 'Bearer ' +  session.token }}
      
      const path = `${RESOURCE_URL}/wp-json/hot/v2/groups/`;
      try {
      const results = await fetch(path , headers )
      const jsonRes = await results.json();
        dispatch({
          type: GET_GROUPS,
          payload: jsonRes.data
        })
      } catch {
        console.log("ERROR")
        dispatch({
          type: GROUP_ERROR,
          payload: "ERROR"
        });
      }
      
    }
  }
  // Get Groups
  const getGroup = async ( id ) => {
    //setLoading();
    const session = JSON.parse(localStorage.getItem('session'));
    if ( session ) {
      headers = { headers : { 'Content-Type': 'application/json; charset=utf-8', 'authorization': 'Bearer ' +  session.token }}
      
      const path = `${RESOURCE_URL}/wp-json/hot/v2/groups/${id}`;
      try {
      const results = await fetch(path , headers )
      const jsonRes = await results.json();
        dispatch({
          type: GET_GROUP,
          payload: jsonRes.data
        })
      } catch {
        console.log("ERROR")
        dispatch({
          type: GROUP_ERROR,
          payload: "ERROR"
        });
      }
      
    }
  }

  // Check for status Errors 
  const checkedForErrors = data => {
    if (data.data) {
      return ( ( data.data.status && data.data.status === 403 ) ? false: true );
    }
    return true;    
  }

  //Create New Group 
  const addNewGroup = async (data , uID) => {
    const session = JSON.parse(localStorage.getItem('session'));
    // const user_id =  this.context.state.userData.id;
    const path = `${RESOURCE_URL}/wp-json/hot/v2/groups/${uID}`;
    const results = await fetch( path, {
      method      : 'POST',
      headers     : { 'Content-Type': 'application/json; charset=utf-8', 'authorization': 'Bearer ' +  session.token },
      body: JSON.stringify(data)
    })
    const jsonRes = await results.json();
    console.log(jsonRes)
    dispatch({
      type: ADD_NEW_GROUP,
      payload   : jsonRes.groups.data
    })
     dispatch({ type: SET_LOADING });
  }

  // Add Teacher to a group
  const addNewUserToGroup = async ( uID , gID ) => {
    const session = JSON.parse(localStorage.getItem('session'));
    const path = `${RESOURCE_URL}/wp-json/hot/v2/groups/users/${uID}?groupid=${gID}`;
    let data=[];
    // datagroupid = gID;
    const results = await fetch( path, {
      method      : 'POST',
      headers     : { 'Content-Type': 'application/json; charset=utf-8', 'authorization': 'Bearer ' +  session.token },
      body: data
    })
    const jsonRes = await results.json();
    dispatch({
      type: ADD_NEW_USER_TO_GROUP,
      payload: jsonRes
    })
  }

  // remove user from group 
  const removeUserFromGroup = async (data ) => {
    const session = JSON.parse(localStorage.getItem('session'));
    const path = `${RESOURCE_URL}/wp-json/hot/v2/students/`;
    const results = await fetch(
      path, {
        method       : 'POST',
        headers      : { 'Content-Type': 'application/json; charset=utf-8', 'authorization': 'Bearer ' +  session.token },
        body: JSON.stringify(data)
      }
    );
    const resJson = await results.json();
    if ( resJson.status !== 403 ) {
      
      dispatch( { type : DELETE_USER_FROM_GROUP , payload: resJson })
    } else {
      dispatch( { type : GROUP_ERROR , payload : resJson.error })
    }
  }
  
  // Filter users  by first or last name
  const filterUsers = text => {
   dispatch( { type: FILTER_USERS , payload : text })
  }

  // select only Teachers
  const filterTeachers = checked => {
   dispatch( { type: FILTER_TEACHERS , payload : checked })
  }

  //Clear all Filters
  const clearFilter = () => {
    dispatch( { type: CLEAR_FILTER })
  }
  
  return (
    <UsersContext.Provider
      value={{
        users : state.users,
        groups: state.groups,
        currentGroup: state.currentGroup,
        loading: state.loading,
        userGroupData: state.userGroupData,
        filtered: state.filtered,
        groupError: state.groupError,
        total : state.total,
        filterUsers,
        filterTeachers,
        clearFilter,
        getGroups,
        getGroup,
        addNewGroup,
        addNewUserToGroup,
        getAllUsers,
        registerUser,
        getUserProfile,
        deleteUserProfile,
        removeUserFromGroup,
        currentUser : state.currentUser
      }} >
    {props.children}
    </UsersContext.Provider>
  )
};


export default UsersState;



