import React, {  Fragment, useContext } from 'react'
import { Link } from 'react-router-dom';
// import Spinner from '../layout/Spinner';
// import MaterialIcon from 'material-icons-react';
import AuthContext from '../../context/auth/authContext';
import './login.css';


const NotLoggedIn = props => {

  const authContext = useContext(AuthContext);
  const {  loadAdmin , userError, error } = authContext;
  
  // const clearCache = ( e ) =>{
  //   console.log("ClearCache");
  //   localStorage.clear();
  //   document.getElementById('clear-btn').innerHTML = "Cache geleegd, ververs pagina";
  // }
  const submitHandler = e => {
    e.preventDefault();
    const data = new FormData(e.target);
    loadAdmin(data);
  }
  let errorMsg = "";
  if (userError) {
    console.log(userError);
    switch (userError.code) {
      case '[jwt_auth] invalid_username':
        errorMsg = 'Gebruikersnaam onbekend';
        break;
      case '[jwt_auth] incorrect_password':
        errorMsg = "Wachtwoord incorrect";
        break;
      default:
        errorMsg = userError.code;
    }
  }
  if (error) {
    console.log(error);
    switch (error.code) {
      case 'rest_user_invalid_id':
        errorMsg = 'Gebruikersnaam staat niet geregistreerd';
        break;
      case '[jwt_auth] invalid_username':
          errorMsg = 'Gebruikersnaam onbekend';
          break;
      case '[jwt_auth] incorrect_password':
          errorMsg = "Wachtwoord incorrect";
          break;
      default:
        errorMsg = error.message;
    }
  }
  return (
    <Fragment>
      <section className="form">  
        <header className="course__header"><h2>Reeds geregistreerd?</h2></header>    
        <form onSubmit={ submitHandler }>
        { userError  ? <div className="error">{errorMsg}</div> : "" }
        { error  ? <div className="error">{errorMsg}</div> : "" }
          <section className="form__item" >
            <label htmlFor="username">Gebruikersnaam</label>
            <input id="username" name="username" required type="text" />
          </section>
          <section className="form__item">
            <label htmlFor="password">Wachtwoord</label>
            <input id="password" name="password" required type="password" />
          </section>
          <section className="form__item" >
            <button className="btn" >Inloggen</button>
          </section>
        </form>
      </section>         
        <section className="form">
          <header className="course__header"><h2>RegistratieCode:</h2></header>
          <section className="form__item" >
          <p>Log in met registratie code</p>
          <span className="btn">{ <Link to={'/'}>Ga naar de registratie pagina</Link>}</span>
              {/* <button className="btn" id='clear-btn' onClick={ this.clearCache.bind(this)}>Leeg Cache</button> */}
          </section>
      </section>
    </Fragment>
  )
}

export default NotLoggedIn;