import React, { useReducer } from 'react';

import ProgressContext from './progressContext';
import ProgressReducer from './progressReducer';
import { GET_PROGRESS ,UPDATE_META, CLEANUP_PROGRESS, PROGRESS_ERROR, START_PROGRESS , RESOURCE_URL, UPDATE_PROGRESS } from '../types';


const ProgressState = props => {
  
  const newMeta  = {
    time: { "seconds": 0, "percent": 0, "duration": 0 },
    actions : [{ type:'Start', timestamp: 0, data:"Video Monitor gestart" }]
  };
  const currentMeta = newMeta;

  //Set State
  const initialState = {
    progress: null,
    loading: true,
    currentVideo: null,
    videoList : [],
    courseID: null,
    currentIndex: 0,
    currentProgressID: 0,
    currentProgress: null,
    currentMeta: [],
    perc_viewed: 0,
    currentCourse: null,
    total: 0,
    offset: 0,
    error : null
  }

  // Constants used throughout

  const session = JSON.parse(localStorage.getItem('session'));
  let headers;

  if( session )
   headers = { headers: { 'Content-Type': 'application/json; charset=utf-8', 'authorization': 'Bearer ' + session.token } };

  // Connect to Reducer
  const [state, dispatch] = useReducer(ProgressReducer, initialState);
  
  const checkedForErrors = data => {
    if (data.data) {
      return ( ( data.data.status===404 && data.data.status === 403 ) ? false: true );
    }
    return true;    
  }

 // Get Progress
  const getProgress = async (id, limit = 25, offset = 0) => {
    const session = JSON.parse(localStorage.getItem('session'));
    if (session) {
      headers = { headers: { 'Content-Type': 'application/json; charset=utf-8', 'authorization': 'Bearer ' + session.token } };
      const path = `${RESOURCE_URL}/wp-json/hot/v2/progress/user/${id}?limit=${limit}&offset=${offset}`;
      const res = await fetch(path, headers);
      const jsonRes = await res.json();
      console.log(jsonRes)
      if ( checkedForErrors( jsonRes ) && jsonRes.data.status !== 403 ) {
        dispatch( { type : GET_PROGRESS , payload : jsonRes })
      } else {
        dispatch({ type: PROGRESS_ERROR, payload: jsonRes });
      }
    }
  }
  
// clean up Progress
  const cleanUpProgress = () => {
    dispatch({ type: CLEANUP_PROGRESS });
  }


  // Progress started when new video is requested
  const startVideoProgress = async (videoPost, user_id, course_id) => {
    // console.log(videoPost)
    if( videoPost && videoPost.post ){
      const title = videoPost.post.post_title;
      document.title = title;
      const path = `${RESOURCE_URL}/wp-json/hot/v2/progress/${user_id}`;
      const response = await fetch( path, {
          method      : 'POST',
          headers     : { 'Content-Type': 'application/x-www-form-urlencoded; charset=utf-8', 'authorization': 'Bearer ' +  session.token },
        body: 'user_id=' + user_id + '&title=' + videoPost.post.post_title + '&course_id=' + course_id + '&video_id=' + videoPost.post.ID + '&meta=' + JSON.stringify( newMeta ) 
      })
      const resJson = await response.json();
      if (resJson.data) {
        dispatch({ type : START_PROGRESS , payload : resJson.data });
      } else {
        dispatch({type: PROGRESS_ERROR, payload: resJson});
      }
    }
    
  }

  // Called by timer in Player
  const updateVideoProgress = async (videoObject, user_id) => {
    let perc_viewed = parseInt(videoObject.time.percent * 100);

      let data = JSON.stringify(videoObject);
      const token = session.token;
      const path = `${RESOURCE_URL}/wp-json/hot/v2/progress/${user_id}`;
     await fetch( path, {
         method       : 'POST',
         headers      : { 'Content-Type': 'application/x-www-form-urlencoded; charset=utf-8', 'authorization': 'Bearer ' +  token },
        body: 'update=' + state.currentProgressID + '&meta=' + data + '&perc_viewed=' + perc_viewed
      })
        // const  resJson = await result.json()
        dispatch( { type : UPDATE_PROGRESS , payload : perc_viewed })
        // dispatch( { type : UPDATE_PERC , payload : perc_viewed })
        
  }
  // Update Meta called by any interaction of Player
  const updateMeta = data => {
    console.log("Update Meta")
    dispatch({ type: UPDATE_META, payload: data });

  }
  
  return (
    <ProgressContext.Provider
      value={{
        progress: state.progress,
        loading: state.loading,
        perc_viewed: state.perc_viewed,
        error : state.error,
        getProgress,
        updateVideoProgress,
        startVideoProgress,
        currentMeta,
        cleanUpProgress,
        updateMeta,
        total: state.total,
        offset : state.offset ,
        currentProgressID: state.currentProgressID
      }} >
    {props.children}
    </ProgressContext.Provider>)
};


export default ProgressState;