import React, {  useContext ,useEffect } from 'react';
import { Link } from 'react-router-dom';
import MaterialIcon from 'material-icons-react';
import Spinner from '../layout/Spinner';
// import Pagination from './Pagination';
// import CoursesContext from '../../context/courses/coursesContext';
import ProgressContext from '../../context/progress/progressContext';
import UsersContext from '../../context/users/usersContext';



const ViewedList = props => {
  // const coursesContext = useContext(CoursesContext);
  const progressContext = useContext(ProgressContext);
  const usersContext = useContext(UsersContext);
  // const { courses } = coursesContext;
  const { getProgress, progress, total } = progressContext;
  const { currentUser } = usersContext;

  useEffect(() => {
    if (currentUser) getProgress(currentUser.id, 20);
    // eslint-disable-next-line
  }, []);
  if( progress ){
    if( progress[0] === 'empty'){
      return(<div>Geen video's bekeken</div>)
    } else{
      let el = [];
      console.log(progress);
      if ( Array.isArray(progress)) {
        progress.map((e, index) => {
          const viewed = '100%';
          // const viewed = e.perc_viewed + '%';
          let divStyle = {
            color: 'white',
            width: viewed,
            WebkitTransition: 'all', // note the capital 'W' here
            msTransition: 'all' // 'ms' is the only lowercase vendor prefix
          };
          el.push(
            <tr key={index} className="viewed__list__item">
              <td><Link to={`/video/${e.course_id}/${e.video_id}`}><MaterialIcon icon="play_circle_filled" />{e.videoTitle}</Link></td>
              <td><div className="progress-bar"><div className="bar" style={divStyle}></div>
                {/* {e.perc_viewed} */}
              </div></td>
              <td>{e.last_viewed}</td>
            </tr>)
          return el;
        }
        )
      } else {
        el.push(<tr key={0} className="viewed__list__item">
          <td>Nog geen video's bekeken</td>
          <td></td>
          <td></td>
        </tr>)
      }
      return (
        <section className="dashboard">
          <header className="dashboard-item-header">
            <h4> { total } bekeken video's</h4>
          </header>
          <div>
            <table className="viewed__list">
              <thead><tr>
                <th>Video Titel</th>
                <th>Voortgang</th>
                <th>Laatst bekeken</th>
              </tr>
              </thead>
              <tbody>
                {el}
              </tbody>
            </table>
            {/* <Pagination controller={props.list} navigate={ props.navigate} getNewProgress={props.getNewProgress} /> */}
          </div>
      </section>
    ) }
  } else { return ( <div><Spinner/></div> ) }
}

export default ViewedList;