import React , { Component } from 'react';
import { BrowserRouter as Router , Route, Switch } from "react-router-dom";

import Navbar from "./components/layout/Navbar";

import Index from "./components/layout/Index";
import ProgressState from './context/progress/ProgressState';
import UsersState from './context/users/UsersState';
import CoursesState from './context/courses/CoursesState';
import AuthState from './context/auth/AuthState';
// import Cart from "./components/cart/Cart";
import Coursedetail from "./components/courses/Coursedetail";
import PlayerContainer from './components/playerv2/PlayerContainer';
import Login from './components/login/Login';
import Administratie from './components/administration/Administratie';
import Groups from './components/groepsbeheer/Groups';


import './App.css';

let root = document.getElementById('root');
const basename = root.dataset.domain;


class App extends Component {
    
  render() {
    return (
       <AuthState>
        <CoursesState>
          <ProgressState>
            <UsersState>
              <Router basename={'/' + basename}>
                <React.Fragment>
                  <Navbar />
                  <main className="main-window">
                    <Switch>
                      <Route exact path="/" component={Index} />
                      <Route path="/cursus/:id" component={Coursedetail} />
                      {/* <Route path="/winkelmand" component={ Cart } /> */}
                      <Route path="/login" component={Login} />
                      <Route path="/video/:course_id/:id" component={PlayerContainer} />
                      <Route path="/administratie" component={Administratie} />
                      <Route path="/groepsbeheer" component={Groups} />
                    </Switch>
                  </main>
                
                </React.Fragment>
              </Router>
            </UsersState>
          </ProgressState>
        </CoursesState>
      </AuthState>
    );
  }
}

export default App;
