import React, { useContext,  useState } from 'react';
import UsersContext from '../../../context/users/usersContext';


const AddToGroup = () => {
  const usersContext = useContext(UsersContext);
  const { groups, addNewUserToGroup, currentUser } = usersContext;
  
  const [group, setGroup] = useState('');




  const onSubmit = e => {
    e.preventDefault();
    const uID = currentUser.id;
    console.log(uID , group);
    addNewUserToGroup ( uID ,group )
  }

  const onChange = e => {
    setGroup(e.target.value);
   
  }


  let groupList;
  if (groups) {
    const userGroups = currentUser.groups;
    groupList = groups.map(item => {
      const found = userGroups.find(uGroup => uGroup.group.group_id === item.id);
      if (found) {
        return ( <option key = { item.id } value = { item.id } disabled > { item.group.name }</option > )
      } else {

        return ( <option key = { item.id } value = { item.id } > { item.group.name }</option > )
      }
    } );
  
  }
  return (
    <div className="add_to_group disabled">
      <h3>Voeg toe aan een nieuwe groep</h3>
      <form onSubmit={ onSubmit } >
        <section className="form__item">
            <label htmlFor="select1" ><span className="alert">* </span>Selecteer groep:</label>
          <select className="select-css" name="group" onChange={onChange} value={ group } required >{ groupList }</select>
        </section>
        <section >
          <button className="btn important_btn" disabled>Voegtoe aan Groep</button>
        </section>
      </form>
    </div>
  )
}

export default AddToGroup
