import React , { useContext , useEffect , useState } from 'react';
import AuthContext from '../../../context/auth/authContext';
import CoursesContext from '../../../context/courses/coursesContext';
import DashboardCourseItem from '../DashboardCourseItem';

const SubscriptionsList = () => {
  const authContext = useContext(AuthContext);
  const coursesContext = useContext(CoursesContext);

  const { subscriptions } = authContext;
  const { courses, getCourses } = coursesContext;
  
  // const [subscriptionList, setSubscriptionList] = useState({});
  const [coursesList, setCoursesList] = useState([]);
  // let subs = [];

  useEffect( () => {
    const total = parseInt(subscriptions.cursussen);
    if (!courses ) {
      getCourses();
    } else {
      let coursesOBJ = [];
      for (let i = 0; i < total; i++) {
        const courseID = parseInt(subscriptions["cursussen_" + i + "_cursus_id"]);
        const found = courses.find(course => {
          return course.id === courseID;
        });
        coursesOBJ.push(found);
      }
      setCoursesList(coursesOBJ);
    }
    // eslint-disable-next-line
  }, [courses , subscriptions]);


  return (
    <section className="course__grid">
      { coursesList.map((course, id) => {
        return <DashboardCourseItem course={course} key={id} />
      })
      }
    </section>
  )
}
export default SubscriptionsList;

