import React, { Fragment, useContext, useEffect } from 'react';
import { Link } from 'react-router-dom';
import UsersContext from '../../context/users/usersContext';
import AuthContext from '../../context/auth/authContext';
import CoursesContext from '../../context/courses/coursesContext';

import { SITE_TITLE } from '../../context/types';

import Spinner from '../layout/Spinner';
import GroupItem from'./GroupItem';
import './Groups.css';
import Modal from '../layout/Modal';


const Groups = props => {

    const usersContext = useContext(UsersContext);
    const authContext = useContext(AuthContext);
   const coursesContext = useContext(CoursesContext)
    const { getGroups, groups } = usersContext; 
    const { user } = authContext;
    const { courses, getCourses } = coursesContext;
   

    useEffect(() => {
        getGroups();
        if (!courses) {
            getCourses();
        }
        // eslint-disable-next-line
    }, [courses]);
   
    // const handleChange = e => {
    //     // this.setState({description: event.target.value});
    //     console.log( e )
    // }


    // const onClose = () => {
    //     // this.setState({ show : false })
    // }
    

    
    const findTutorGroup = ( group ,id ) =>{
        if( group.id !== '1' ){
        return( group.users.find(  user  => {
            if( user.id === parseInt(id) ){
                return true;
            } else {
                return false;
            }
            } ) )

        } else{
            return false;
        }
    }
    const findCourses = ( coursesList , courses ) => {
        let list =[];
        coursesList = coursesList.split(',');
        const allCourses = courses;
        coursesList.forEach( course =>{
            allCourses.find( (allCourse) =>{
                if(parseInt(allCourse.id) === parseInt(course )){
                    list.push(allCourse);
                }
                return null;
            })
        })
        return list;
    }
   
    if( user ){
        if (user.roles === "docent" || user.roles === "instelling") {
            document.title = `Groepsbeheer - ${ SITE_TITLE }`;
            const groupItems = [];
            if( groups && groups.length > 0 ){
                groups.forEach( 
                    ( group , index ) => { 
                        if( user.roles === "docent" ){ 
            
                            // if(findTutorGroup( group , user.id )){
                                const coursesgroup = findCourses( group.courses ,courses  )
                                groupItems.push(<GroupItem group={group} courses ={coursesgroup} key={index} /> ) 
                            // }
                        } else {
                            groupItems.push(<GroupItem group={group} key={index} /> ) 
                        }
                    } 
                )
            } else {
                groupItems.push( <span key="0" ><h4>Groepen worden geladen...</h4><Spinner /></span> );
            }
            return (
                <Fragment>
                    <div className="container">
                      
                        <section className="dashboard-item">
                            <header className="card-header"><h1>Groepen</h1></header>
                            <article className="dashboard-item-content">
                                <section className="groups__grid">{groupItems}</section>
                            </article>
                            <footer className="dashboard-item-footer"></footer>
                        </section>
                    </div>
                    { <Modal /> }
                </Fragment>
            )
            } else {
                return (
                    <div className="container">
                        <header className="card-header"><h2>Geen Toegang</h2></header>
                        <main>
                            <p>U heeft omvoldoende rechten om deze pagina te kunnen bekijken.</p>
                            <p>Ga terug naar de <Link to="/">Start</Link>pagina om in te loggen</p>
                        </main>
                    </div>
                )
            }
        } else {
            return (
                <div className="container">
                    <header className="card-header"><h2>Geen Toegang</h2></header>
                    <main className="dashboard center-warning">

                        <h4>U heeft omvoldoende rechten om deze pagina te kunnen bekijken.</h4>
                        <p>Ga terug naar de <Link to="/">Startpagina</Link> om in te loggen</p>
                    </main>
                </div>
            )
        }
  
}
export default Groups;