import React, { useContext } from 'react'
import MaterialIcon from 'material-icons-react';
import UsersContext from '../../context/users/usersContext';

const AllListItem = props => {

  const usersContext = useContext(UsersContext);
  const { getUserProfile } = usersContext;


  const onClick = (e) => {
    e.preventDefault();
    getUserProfile(props.user);
  }


  
  if (props.user && props.user.role ) {
    const user = props.user;
    const groups = user.groups;
    let groupList = '';
    if (groups) {
      for (let i = 0; i < groups.length; i++) {
        if (i === 0) {
          groupList += (i + 1) + ': ' + groups[i].group.name;
        } else {
          
          groupList += ', ' + (i + 1) + ': ' + groups[i].group.name;
        }
      }
    }
    
    let status_name = '';
    let code = '';
    if (props.user.role === "student") {
      if (user.subscriptions.length > 0) {
        const subscription = user.subscriptions[0];
        let status = subscription.active;
        let title = "Status: In afwachting";
        code = subscription.code;
        if (status === null || status === "0") {
          status = 0;
        } else if (status === '1') {
          title = "Status: Actief";
        } else {
          title = "Status: Inactief";
        }
        status_name = <span title={title} className={'state state_' + status}></span>;
      }
      return (
        <tr className="user-list__item" key={user.ID} >
          <td className="user-list__icon"><MaterialIcon icon="person_outline" color="var(--primary-color)" size="16px" title="Student"></MaterialIcon></td>
          <td className="user-list__name"><div id={user.id} onClick={onClick} ><strong>{props.user.firstname} {props.user.lastname}</strong> <span className='small'>[{user.display_name}]</span></div></td>
          <td className="user-list__code">{code}</td>
          <td className="user-list__date" >{groupList}</td>
          <td className="user-list__icon">{status_name}</td>
        </tr>
      )
    } else if (props.user.role === "docent") {
      return (
        <tr className="user-list__item" key={user.ID} >
          <td className="user-list__icon"><MaterialIcon icon="account_circle" color="var(--secondary-color)" title="Docent" size="24" ></MaterialIcon></td>
          <td className="user-list__name"><div id={user.id} onClick={onClick} ><strong>{props.user.firstname} {props.user.lastname}</strong> <span className='small'>[{user.display_name}]</span></div></td>
          <td className="user-list__code"><strong>Docent</strong></td>
          <td className="user-list__date" >{groupList}</td>
          <td className="user-list__icon"><span title="Status: Actief" className="state state_1" ></span></td>
        </tr>
      )
    } 
  } else { return <tr><td>Laden...</td></tr> }
}
export default AllListItem;
