import {
  USER_LOADED,
  AUTH_ERROR,
  LOGIN_SUCCESS,
  WAS_LOGGED_IN,
  NOT_LOGGED_IN,
  LOGIN_FAIL,
  LOGIN_ERROR,
  REGISTER_FAIL,
  CLEAR_ERRORS,
  // REGISTER_USER,
  LOGOUT,
  ADMIN_LOADED
} from '../types';

const AuthReducer = (state, action) => {
  let user;
  let session;
  let subscriptions;
  let currentSubscription;
  switch (action.type) {
    case USER_LOADED:
      user = action.payload.registration.userobject;
      session = action.payload.registration.session;
      subscriptions = action.payload.registration.subscriptionmeta;
      // currentSubscription = action.payload.registration.subscription.post_title;
      localStorage.setItem('userData', JSON.stringify(user));
      localStorage.setItem('session', JSON.stringify(session));
      localStorage.setItem('subscriptions', JSON.stringify(subscriptions));
      // localStorage.setItem('currentSubscription', JSON.stringify(currentSubscription));
      //localStorage.setItem( 'userData' , action.payload )
      return {
        ...state,
        isAuthenticated: true,
        loading: false,
        user: user,
        subscriptions: subscriptions,
        currentSubscription :currentSubscription,
        error : null
      }
    case ADMIN_LOADED:
      user = action.payload;
      if ( Array.isArray( user.roles )) {
        user.roles = user.roles[0];
      }
      localStorage.setItem('userData', JSON.stringify(user));
     
      // localStorage.setItem('subscriptions', JSON.stringify([]) );
      return {
        ...state,
        isAuthenticated : true,
        user: user,
        subscriptions: null,
        currentSubscription : null,
        loading: false,
        error : null
      }
    case LOGIN_SUCCESS:
      user = action.payload.registration.userobject;
      session = action.payload.registration.session;
      subscriptions = action.payload.registration.subscriptionmeta;
      // currentSubscription = action.payload.registration.subscription.post_title;
      localStorage.setItem('userData', JSON.stringify(user));
      localStorage.setItem('session', JSON.stringify(session));
      localStorage.setItem('subscriptions', JSON.stringify(subscriptions));
      // localStorage.setItem('currentSubscription', JSON.stringify(currentSubscription));
      // console.log( action.payload.registration )
      return {
        ...state,
        isAuthenticated: true,
        user: user,
        subscriptions: subscriptions,
        // currentSubscription : currentSubscription,
        loading: false,
        error : null
      }
    case LOGOUT:
      localStorage.removeItem('userData');
      localStorage.removeItem('session');
      localStorage.removeItem('subscriptions');
      return {
        ...state,
        isAuthenticated: false,
        loading: false,
        user: null,
        error: null,
        subscriptions: null,
        currentSubscription : null
      }
    case NOT_LOGGED_IN:
      
      return {
        ...state,
        isAuthenticated: false,
        loading: false,
        error: null,
        user: null,
        subscriptions: null,
        // currentSubscription : null
      }
    case WAS_LOGGED_IN:
      const userData = JSON.parse( localStorage.getItem( 'userData' ) )
      let subs = [];
      if (localStorage.getItem("subscriptions") !== null) {
        subs = JSON.parse(localStorage.getItem('subscriptions'));
      }
        return { 
          ...state,
          isAuthenticated: true,
          loading: false,
          error: null,
          user: userData,
          subscriptions: subs,
          // currentSubscription : null
        }
    case LOGIN_FAIL:
    case LOGIN_ERROR:
    case AUTH_ERROR:
    case REGISTER_FAIL:
      localStorage.removeItem('session');
      localStorage.removeItem('userData');
      localStorage.removeItem('subscriptions');
      return {
        ...state,
        isAuthenticated: false,
        loading: false,
        error: action.payload,
        user: null,
        subscriptions: null,
        // currentSubscription : null
      }
    case CLEAR_ERRORS:
      return {
        ...state,
        error: null
      }
    default:
      return state;
  }
}
export default AuthReducer;