import React, {   useContext } from 'react';
import MaterialIcon from 'material-icons-react';

import { NavLink, Redirect } from 'react-router-dom';

import AuthContext from '../../context/auth/authContext';
import Logo from './Logo';


const Navbar = props => {

  //UseContext
  const authContext = useContext(AuthContext);
  const { isAuthenticated, user, logOut } = authContext;


  // Logout ClickHandler
  const onLogoutClick = (e) => {
    e.preventDefault();
    logOut();
   return <Redirect to='/'/>
  }

  // is USer Logged In?
  if (!isAuthenticated) {
    return (
      <nav className="course-header sticky" id="course-navbar">
        <NavLink to="/" style={{ 'left': '10%' }}>{<Logo />}</NavLink>
      </nav>
    )
  } else { // Is Logged In....
    switch (user.roles) {
      case "instelling":
        return (
          <nav className="course-header sticky" id="course-navbar">
            <NavLink to="/administratie" style={{ 'left': '10%' }}>{<Logo />}</NavLink>
            <section>
              <NavLink to="/administratie" className="btn"> <MaterialIcon icon="verified_user" color="#white" /> Administratie</NavLink>
              <NavLink to="/groepsbeheer" className="btn"> <MaterialIcon icon="group" color="white" /> Groepsbeheer</NavLink>
              <NavLink to="/login" className="btn"> <MaterialIcon icon="account_circle" color="white" />
                <span className="username" >{user.name}</span></NavLink>
            </section>
          </nav>
        )
      //  break;
      case "docent":
        return (
          <nav className="course-header sticky" id="course-navbar">
            <NavLink to="/" style={{ 'left': '10%' }}>{<Logo />}</NavLink>
            <section className="navbar-right">
              <NavLink to="/groepsbeheer" className="btn"> <MaterialIcon icon="verified_user" color="white" /> Groepsbeheer</NavLink>
              <button className="btn" onClick={onLogoutClick}>Afmelden </button>
            </section>
          </nav>
        )
      default:
        return (
          <nav className="course-header sticky" id="course-navbar">
            <NavLink to="/" style={{ 'left': '10%' }}>{<Logo />}</NavLink>
            <section className="navbar-right">
              <button className="btn" onClick={onLogoutClick} >Afmelden </button>
            </section>
         </nav>
        )
    }
  }
}


export default Navbar;
