import React , { useContext ,useEffect } from 'react';
import GroupItem from './GroupItem';
import Spinner from '../layout/Spinner';
import UsersContext from '../../context/users/usersContext';


const AllGroups = (props) => {

  const usersContext = useContext( UsersContext );
  const { getGroups ,groups } = usersContext;

  useEffect(() => {
    getGroups();
    // eslint-disable-next-line
  }, []);
  
  const groupItems = [];
  if( groups && groups.length > 0 ){
    groups.forEach((group, index) => {
      groupItems.push(
      <GroupItem 
        group={group} 
        key={index}
        // deleteHandler={deleteHandler} 
        // showUserProfile={ showUserProfile } 
      /> ) } )
    } else {
      groupItems.push( <span key="werwrevhtuy" ><h4>Groepen worden geladen...</h4><Spinner /></span> );
    }
  
  return (
    <section className="dashboard-item">
      <header className="dashboard-item-header"><h3>Groepen: ({ groups ? groups.length : 0 })</h3></header>
      <article className="dashboard-item-content">
        <section className="groups__grid">{groupItems}</section>
      </article>
    </section>
  )

}
export default AllGroups;
