import React from 'react';

import { NavLink } from 'react-router-dom';

const Linker = props => {
 
  const { currentCourseID } = props;

    const link = props.link;

    let title = link.post.post_title;
   
    const post_id = link.post.ID;
    
    return (
      <li className="chapter__list-item">
      <NavLink to={`/video/${currentCourseID}/${post_id}` } id={'video_'+post_id} className="chapter__list-item__link">{ title }</NavLink>
      </li>
    )
  
}

export default Linker;
