import React, { useReducer } from 'react';
import CoursesReducer from './coursesReducer';
import CoursesContext from './coursesContext';
import { API_URL, GET_COURSES , SET_LOADING, SET_COURSE, GET_VIDEO, SET_CURRENT_VIDEO  } from '../types';


const CoursesState = props => {

  //Set State
  const initialState = {
    courses: null,
    loading: false,
    currentCourse: null,
    currentCourseID : null,
    currentVideo: null,
    currentVideoID: null,
    currentIndex: 0,
    currentMeta: null,
    videoList : null
  }

    // Connect to Reducer
  const [state, dispatch] = useReducer(CoursesReducer, initialState);
  
  // Get Courses

  const getCourses = async (data) => {
    if (localStorage && localStorage.getItem('courses')) {
      dispatch({
        type: GET_COURSES,
        payload: JSON.parse( localStorage.getItem('courses'))
      });
    } else {
      setLoading();
      const results = await fetch(API_URL+ 'courses?per_page=100' )
      const jsonRes = await results.json();
      localStorage.setItem('courses', JSON.stringify(jsonRes));
      dispatch({
        type: GET_COURSES,
        payload: jsonRes.data
      });
    }
  }

  // get single Course from 
  const getCourse = async (courseId, videoId) => {
    // console.log('Get Course');
    const results = await fetch(API_URL + 'courses/' + courseId );
    const json = await results.json();
    // console.log( json );
    const obj = { 'course': json, 'courseId': courseId, 'videoId': videoId } ;
    dispatch({ type: SET_COURSE, payload: obj });
  }

  // get the Video
  const getVideo = async (videoId, currentCourse) => {
    const currentVideo = await findVideo(videoId, currentCourse);
    const videoObj = {
      id: videoId,
      currentVideo : currentVideo
    }
    
    dispatch({ type: GET_VIDEO, payload : videoObj })
  }

  const setCurrentVideo = currentvid => {
    dispatch({ type: SET_CURRENT_VIDEO, payload: currentvid });
  }

  const findVideo = ( videoID , currentCourse ) => {
    if (currentCourse) {
      
      // console.log(currentCourse.chapters);
      const chapters = state.currentCourse.chapters;

      for ( let chapter in chapters ) {
        const videoList =  Object.values(chapters[chapter].video);
        for( let item in videoList ){ 
          //videoListItems.push( videoList[item] );
          if (parseInt(videoList[item].post.ID) === parseInt(videoID)) { 
            // console.log(videoList[item]);
            return videoList[item]
          };
        }
        
      }
    } else {
      return null;
    }
    //this.setState( { videoList : videoListItems });
  }

  //Set Loading
  const setLoading = () => dispatch( { type: SET_LOADING } );

  // Return the Element
  return (
    <CoursesContext.Provider
      value={{
        courses: state.courses,
        loading: state.loading,
        currentCourse: state.currentCourse,
        currentVideo: state.currentVideo,
        currentCourseID: state.currentCourseID,
        videoList: state.videoList,
        setCurrentVideo,
        getCourse,
        getVideo,
        getCourses
      }} >
      {props.children}
    </CoursesContext.Provider>
  );
}

export default CoursesState;