import React, {  useContext , useEffect } from 'react';
import Users from './Users';
import { SITE_TITLE } from '../../context/types';

import NewGroup from './NewGroup';

import './Administratie.css';
import AllGroups from './AllGroups';

import Spinner from '../layout/Spinner';
import Modal from './Modal';
import AuthContext from '../../context/auth/authContext';


const Administratie = props => {

  const authContext = useContext(AuthContext);

  const { isAuthenticated, user, isLoggedIn } = authContext;
  
  useEffect(() => {
    if (!isAuthenticated) {
      isLoggedIn()
    }
    // eslint-disable-next-line
  },[ isAuthenticated ])
  // const  findIndex = ( id ) => {
  //     const list = this.state.courses;
  //     for ( let index in list ) { if( list[index].id === id ){ return  index } };
  //   }

  if ( isAuthenticated ) {
    const title = `Administratie Paneel - ${ SITE_TITLE}`;
    document.title = title;
    const role = user.roles;
     if( role === "instelling" || role=== "administrator") {
      return (
        <div className="container">
          <header className="card-header"><h2>Administratie Paneel</h2></header>
          <main className="dashboard">
            <section className="dashboard__item">
            <input type="checkbox" className="invi-select" id="db1" name="group_select"></input>
            <label htmlFor="db1" className="group__label">
              <header className="dashboard-item-header" ><h3><span className="blob">1</span> Groepen</h3></header>
              </label>
              <section className="dashboard__description"><p>Maak allereerst een groep aan en koppel hieraan cursus(sen).</p></section>
              <article className="dashboard__item-columns">
                <NewGroup />
                <AllGroups />
              </article>
            </section>
            <section className="dashboard__item">
              <Users /> 
            </section>
          </main>
         <Modal />
        </div>
      )
     } else {
       return(
         <React.Fragment>
           <div className="container">
            <header className="card-header"><h2>Geen Toegang</h2></header>
            <main><p>U heeft omvoldoende rechten om deze pagina te kunnen bekijken.</p></main>
           </div>
         </React.Fragment>
       )
     }
      
    } else {
      return(
        <Spinner />
      )
    }
   
  
}
export default  Administratie;

