import { GET_PROGRESS , UPDATE_PROGRESS, UPDATE_META, CLEANUP_PROGRESS, START_PROGRESS , PROGRESS_ERROR } from '../types';

export default (state, action) => {
  
  switch (action.type) {
    case GET_PROGRESS:
      let data = action.payload.data;
      if (action.payload.total === 0) {
        data = "Nog geen Video's bekeken, selecteer een Cursus uit het rechterKolom en start Nu!";
      }
      return {
        ...state,
        progress: data,
        total: action.payload.total,
        offset : action.payload.offset,
        loading : false
      }
    case START_PROGRESS: 
      return {
        ...state,
        loading: false,
        currentProgressID: parseInt(action.payload.id),
        currentProgress: action.payload,
        currentMeta: JSON.parse( action.payload.meta ),
        perc_viewed : action.payload.perc_viewed
      }
    case UPDATE_PROGRESS:
      return {
        ...state,
        loading: false,
        perc_viewed : action.payload
      }
    case CLEANUP_PROGRESS:
      return {
        ...state,
        loading: false,
        progress : null
      }
    case PROGRESS_ERROR:
      return {
        ...state,
        loading: false,
        error : action.payload
      }
    case UPDATE_META:
      return {
        ...state,
        currentMeta : action.payload
      }
    default:
      return state;
  }
}