import { GET_GROUPS, GET_GROUP , SET_LOADING, GET_USERS , CLEAR_FILTER, FILTER_USERS, FILTER_TEACHERS, ADD_NEW_USER_TO_GROUP, GET_USER_PROFILE, DELETE_USER_PROFILE, REGISTER_USER, ADD_NEW_GROUP, GROUP_ERROR, DELETE_USER_FROM_GROUP } from '../types';


const UsersReducer = (state, action) => {
  switch (action.type) {
    case GET_USERS:
      return {
        ...state,
        users: action.payload.users,
        total : action.payload.total,
        loading: false,
        groupError: null
      }
    case GET_USER_PROFILE:
      return {
        ...state,
        userGroupData: action.payload.groups,
        currentUser: action.payload.user,
        loading: false,
        groupError: null
      }
    case ADD_NEW_USER_TO_GROUP:
      const groups = action.payload.data;
      return {
        ...state,
        groups: groups,
        loading: false,
        groupError:  null
      }
    case DELETE_USER_FROM_GROUP:
      console.log(action.payload);
      return {
        ...state,
        loading: false,
        groupError : null
      }
    case DELETE_USER_PROFILE:
      return {
        ...state,
        currentUser: null,
        userGroupData: null,
        loading: false,
        groupError: null
      }
    case REGISTER_USER:
      return {
        ...state,
        users: action.payload,
        loading: false,
        groupError: null
      }
    case GET_GROUPS:
      return {
        ...state,
        groups: action.payload,
        loading: false,
        groupError: null
      }
    case GET_GROUP:
      return {
        ...state,
        currentGroup: action.payload,
        loading: false,
        groupError: null
      }
    case ADD_NEW_GROUP:
      return {
        ...state,
        groups: action.payload,
        loading: false,
        groupError: null
      }
    case SET_LOADING:
        return {
          ...state,
          users:[],
          loading: true,
          groupError: null
      }
    case FILTER_USERS:
      return {
        ...state,
        loading: false,
        filtered: state.users.filter(user => {
          const regex = new RegExp(`${action.payload}`, 'gi');
          return user.firstname.match( regex) || user.lastname.match(regex)
        }),
        groupError : null
      }
    case FILTER_TEACHERS:
      return {
        ...state,
        loading: false,
        filtered: state.users.filter(user => {
          console.log(user.role , action.payload);
          if (action.payload === true) {
            return user.role === 'docent';
          }
          return null;
        })
      }
    case CLEAR_FILTER:
      return {
        ...state,
        loading: false,
        filtered: null,
        groupError: null
      }
    case GROUP_ERROR:
      return {
        ...state,
        loading: false,
        groupError: action.payload,
        currentGroup : null
      }
    default:
      return state;
  }
}
export default UsersReducer;