import React, { useContext, useEffect }from 'react';

import './Courses.css';
import Spinner from '../layout/Spinner';
import { Link } from 'react-router-dom';
import CoursesContext from '../../context/courses/coursesContext';
// import MaterialIcon from 'material-icons-react';

const Coursedetail = props => {

  const courseID = props.match.params.id;
 
  const  coursesContext = useContext(CoursesContext);
  const  { courses , getCourses } = coursesContext;
        
  useEffect(() => {
    if (!courses) {
      getCourses()
    }
    // eslint-disable-next-line
  }, [courses]);

  if( courses === null || courses.length === 0 ){
    return <Spinner />
  } else {
    
    const course = courses.filter( product => product.id === Number(courseID ))[0];
    
    const meta = course.acf;

    const metaData = `<section class="course__detail-meta">
    <span class="course__detail-meta-videos">Aantal Video's: ${meta.aantal_videos}</span>
    <span class="course__detail-meta-time">tijd: ${meta.totale_tijdsduur}</span>
    </section>
    `;
    const videolink = (meta["video-link"]);
    return (
      <section className="course__detail">
        <header className="course__header"><h2>{ course.name}</h2></header>
        <nav><Link to='/'><button className="btn"><span>Terug</span></button></Link></nav>
        <section className="course__detail-content">
          <aside className="course__detail-aside">
            <figure className="course__detail_figure">
            <img src={ course.acf.pictogram } alt="Thumbnail"/>
            </figure>
            <section dangerouslySetInnerHTML={{__html: metaData }} ></section>
            <Link to={`/video/${course.id}/${videolink}`}><button className="btn cta-btn">Start Cursus</button></Link>
          </aside>

          <article className="course-card" >
          <section className="course-card__content" dangerouslySetInnerHTML={{__html: course.acf.info}} />
          </article>
        </section>
        <footer></footer>
      </section>
    )
  }
}

export default Coursedetail;