import { GET_COURSES, SET_LOADING, SET_COURSE, GET_VIDEO, SET_CURRENT_VIDEO } from '../types';

const CoursesReducer = (state, action) => {
  switch (action.type) {
    case GET_COURSES:
      return {
        ...state,
        courses: action.payload,
        loading : false
      }
    case SET_LOADING:
        return {
          ...state,
          loading: true
      }
    case GET_VIDEO:
      const { id, currentVideo } = action.payload;
      // console.log(currentVideo)
      return {
        ...state,
        loading: false,
        currentVideoID: id,
        currentVideo : currentVideo
      }
    case SET_COURSE:
      let chapters = action.payload.course.chapters;
      const videoId = action.payload.videoId;
      console.log(action.payload);
        let videoListItems = [];
        for ( let chapter in chapters ) {
          const videoList = chapters[chapter].video;
          for (let item in videoList) {
            videoListItems.push(videoList[item]);
          }
        }
      return {
        ...state,
        loading: false,
        currentCourse: action.payload.course,
        currentCourseID : action.payload.courseId,
        videoList: videoListItems,
        currentVideoID : videoId
      }
    case SET_CURRENT_VIDEO:
      console.log( 'SET_CURRENT_VIDEO', action.payload )
      return {
        ...state,
        currentVideo: action.payload,
        loading: false,
      }
    default:
      return state;
  }
}
export default CoursesReducer;