import React from 'react';

const divStyle = {
  color: 'white',
 backgroundColor : 'var(--secondary-color)',
 padding : '5px'
};

const ActiveSubscriptionsRowItem = props =>{

  
  // const coursesContext = useContext(CoursesContext);
  // const { courses } = coursesContext;
  // const { currentUser } = usersContext;
 

//   const toggleSubscriptionItem = () => {
// // console.log( "is OPen: " , this.state.isOpen)
//     this.setState({ isOpen : !this.state.isOpen})
//     if( !this.state.isOpen) {
//       return ( <MaterialIcon icon="expand_more" color="white" />)
//     } else {
//       return ( <MaterialIcon icon="expand_less" color="white" />)
//     }
      
//   }
  const courses = props.courses;

    let list=[]
    if( courses && courses.length > 0){
    
      courses.forEach( (element, index) => {        
        let name = element.name;       
        list.push(
          <li key={index}>
          {name}
          </li>
        )
      })
    }
    let status = props.status;
    let title = "Status: In afwachting";
    if( status === null || status === '0'){
      status = 0;
    } else if( status === '1'){
      title = "Status: Actief";
    } else {
      title ="Status: Inactief"; 
    }
    // const status_name = <span title={title} className={'state state_'+status}></span>;
   
    return (
      <div className="grouped" key={props.index } >
        <h4 title="Dit is de inlog code voor de student of docent" style={divStyle}> { props.factuurnummer}   <span title={title} className= {`state state_${status}`} /> </h4>      
        <section className="subscription_courses_list">
          <ol>
          {list}
          </ol>
        </section>
        <button className="btn btn-small disabled" disabled >De-activeer inschrijving</button>
      </div>
    )
}
export default ActiveSubscriptionsRowItem;