import React, { useContext ,useEffect  } from 'react';
import { Redirect, Link } from 'react-router-dom';
import Dashboard from '../dashboard/Dashboard';
import AuthContext from '../../context/auth/authContext';
import { CHECKOUT } from '../../context/types';

const Index = props => {

  const authContext = useContext(AuthContext);

  // Pull out variables from authContext
  const { user, isAuthenticated, isLoggedIn, checkRegistration, error } = authContext;
  

  // ComponentDidMount:
  useEffect(() => {
    //check user is logged in....
    isLoggedIn();
    // eslint-disable-next-line
  }, []);
  
  // Send registrationCode to the Backend
  const sendRegistration = (e) =>{
    e.preventDefault();
    const data = new FormData(e.target);
    checkRegistration(data);
  }
  // Is the user Authenticated?
  if(  !isAuthenticated ){
    // const backgroundStyle = { backgroundImage: "url('http://api2.house-of-training.nl/demo/wp-content/uploads/sites/8/2019/05/intro.jpg')" };
    let msg = '';
    if( error){
      msg = <div className="message error">{ error.status }</div>
    }
      return (
        <div className="login__window">
          <section className="form">
            <header className="card-header"><h2>Voer registratie code in</h2></header>
            <article className="sub-header">
              <p>Om toegang te krijgen tot deze site is een registratie code vereist.</p>
            </article>
            <form onSubmit={ sendRegistration } >          
              <section className="form__item" >
                <label htmlFor="code">Registratiecode</label>
                <input id="code" name="code" required type="text" />
              </section>
              <p>Deze Code heeft u verkregen van uw instelling, en is persoonlijk.</p>
              {msg}
              <section className="form__item" >
                <button className="btn" >Inloggen</button>
                <input type="hidden" name="action" value="checkRegistration"></input>
                <input type="hidden" name="nonce" value={ CHECKOUT }></input>
              </section>
          </form>
          <Link to="/login" className="btn btn-login">Login voor Instelling</Link>
          </section>
        </div>
      )
    } else {
      if (user.roles && user.roles[0] === 'instelling') {
        return <Redirect to='/administratie' />
      } else {
        return <Dashboard  />
      }
    }
  
}
// Index.contextType = Context;
export default Index;