import React, { Fragment } from 'react'
import GroupsTableRow from './GroupsTableRow';



const GroupTable = props => {

  
  // const downloadProgress = ( id ) => {console.log( id )
  //   props.downloadProgress( id );
  // }
  const users = props.users;




  let list=[]
  if( users && users.length > 0){
    users.forEach( (user, index) => { 
      if(user.roles === 'student' || user.roles === 'docent'){ 
        list.push(<GroupsTableRow
          user={user}
          key={index}
        />)
      }      
    }) 

  const divStyle = {
    width : '2%'
  };
   const textStyle = {
    width : '45%'
  };
  return (
    <Fragment>
    <table className="user-list"> 
      <thead>
        <tr >
        <th style={divStyle}></th>
          <th  style={textStyle}>Student/ docent </th>
          <th style={textStyle}>Registratie code: </th>
          <th  style={divStyle}>Voortgang: </th>
          <th  style={divStyle}>Actief: </th>
        </tr> 
      </thead>
      <tbody>
        {list}
      </tbody>
    </table>
   
    </Fragment>
  )
    } else {
      return (
        <div>Lege groep</div>
      )
    }
}

export default GroupTable;
