import React, { useContext , useState } from 'react'
import GroupTable from './GroupTable';
import { Link } from "react-router-dom";
import Spinner from '../layout/Spinner';

import CoursesContext from '../../context/courses/coursesContext';
import { RESOURCE_URL, CHECKOUT } from '../../context/types';

const GroupItem = props => {

  const coursesContext = useContext(CoursesContext);

  const { courses } = coursesContext;
  const [ users , setUsers ] = useState(null);
  const [ showCourses , setShowCourses] = useState(false);
  const [ loading , setLoading] = useState(false);
  const [ currentList, setCurrentList] = useState(null);

  let { name, group_id, description } = props.group.group;

  const showGroupMembers = async () => {
    if( users === null ){
      setLoading(true);
      const session = JSON.parse(localStorage.getItem('session'));
      if ( session ) {
      const headers = { headers : { 'Content-Type': 'application/json; charset=utf-8', 'authorization': 'Bearer ' +  session.token }}
        
        const path = `${RESOURCE_URL}/wp-json/hot/v2/groups/?id=${group_id}`;
        try {
          const results = await fetch(path , headers )
          const jsonRes = await results.json();
          setUsers( jsonRes.data[0].users );
          setLoading(false);
        } catch ( error ) {
          console.log("ERROR");
           setLoading(false);
        }
      }
    } else {
      setUsers(null);
    }
  }
  const getGroupExcel = async( e) => {
     const path = `${RESOURCE_URL}/wp-admin/admin-ajax.php?action=group_csv_pull&id=${ group_id }&nonce=${ CHECKOUT }`;
      try {
        const results = await fetch(path  )
        const jsonRes = await results.json();
        console.log(jsonRes);
        setLoading(false);
      } catch ( error ) {
        console.log("ERROR");
          setLoading(false);
      }
  }


 const showHideCourses = () =>{
   setShowCourses( !showCourses );
 }

  const findCourses = (inputCourses) => {
      let list = [];
      if (inputCourses) {
          inputCourses = inputCourses.split(',');
          inputCourses.forEach(course => {
              courses.find((allCourse) => {
                  if (parseInt(allCourse.id) === parseInt(course)) {
                      list.push(allCourse);
                  }
                  return null;
              })
          })
          return list;
      } else {
          return null;
      }
  }


 
  const foundCourses = findCourses(props.group.courses);
  
  let total = props.group.total_users;
  let courseText = [];
  if (foundCourses && foundCourses.length > 0) {
      foundCourses.forEach(course => {
        const title = course.name.replace(/&amp;/g, "&")
        courseText.push( <li className = "course-item-title"
            key = { course.id } > <Link to = { `cursus/` + course.id }
            className = "course-link"
            title = { `Ga naar ${course.name}` } > { title }({ course.count }
                video 's)</Link></li>);
      });
  }
  let descriptionText = "";
  if (description) {
      descriptionText = <section className = "group__description" > <p > { description } </p></section > ;

  }
  // let icon = <button className="btn" onClick={ this.deleteHandler }><MaterialIcon icon="delete" color="#004475" /></button>;
  if (name === "Registered") {
      return null;
  } else {
      const listShow = < GroupTable users = { users } /> ;
      return ( 
      <article className = "group__item" id = { "group_" + group_id } >
        <input type = "checkbox" className = "invi-select" id = { "Choice" + group_id } name = "course_select" />
        <label htmlFor = { "Choice" + group_id } className = "group__label" >
          <header className = "dashboard-item-header" >
            <h3 > { name } <span className = "circle" title = { 'Aantal studenten: ' + total } > { total } </span></h3 >
          </header>
        </label >
          { loading ? <Spinner /> : null }
           { descriptionText } 
         <button className="btn" onClick={ showGroupMembers }>{ users ? 'Verberg Cursisten' : 'Toon cursisten' }</button>
         <button className="btn" onClick={showHideCourses}>{ showCourses ? 'Verberg Cursussen' : 'Toon cursussen' }</button>
        <section className = "group__description" >
          <section className = "table show" > { listShow } </section>
          <h5 > Deze groep volgt de volgende cursus(sen): </h5> 
          <ul className={`course-listing ${showCourses ? 'show' : 'hide' }`} > {courseText} </ul >
        </section > 
         <a href={`${RESOURCE_URL}/wp-admin/admin-ajax.php?action=group_csv_pull&id=${ group_id }&nonce=${ CHECKOUT }` } className="btn download-link" >Download studenten lijst.</a> 
        {/* <button className="btn " onClick={ getGroupExcel }  > Download StudentenLijst </button > */}
      </article >
      )
  }
}
export default GroupItem;
// GroupItem.contextType = Context;