import React, { useState, useContext } from 'react';
import AdminCoursesSelect from './AdminCoursesSelect';
import UsersContext from '../../context/users/usersContext';
import AuthContext from '../../context/auth/authContext';

const NewGroup = props => {

  const authContext = useContext(AuthContext);
  const usersContext = useContext(UsersContext);
  const { user } = authContext;
  const { addNewGroup } = usersContext;

  const [ groupname , setGroupname ] = useState('');
  const [ description , setDescription ] = useState('');
  const [ courses , setCourses ] = useState([]);

  // update the Description 
  const handleDescriptionChange = (e) =>{
    setDescription(e.target.value);
  }

  // update the Groupname state
  const handleGroupChange = (e) =>{
    setGroupname(e.target.value);
  }

  // update the Courses State
  const handleCourses = val => {
    var index =courses.indexOf(val);
    if (index > -1) {
      courses.splice(index, 1);
    } else {
      const newCourses = [...courses,  val ];
      setCourses(newCourses);
    }  
  }

  // Clear the Form
  const clearForm = () => {
    setGroupname('');
    setDescription('');
    setCourses([]);
  }

  // send the dat to teh server
  const sendForm = (e) =>{
    e.preventDefault();
    const formData = {
      'groupname': groupname,
      'description': description,
      'courses'   : courses.toString()
    }
    addNewGroup(formData, user.id);
    clearForm();
  }
  // console.log(user);
  return (
    <section className="dashboard-item">
      <header className="dashboard-item-header"><h3>Nieuwe Groep</h3></header>
      <article className="dashboard-item-content">
          <form onSubmit={ sendForm} >
              <section className="form__item" >
                  <label htmlFor="groupname"><span className="alert">*</span> Groepsnaam:</label>
                  <input id="groupname" name="groupname" placeholder="nieuwe groepsnaam" required type="text" onChange={ handleGroupChange} value={groupname}/>
              </section>
              <section className="form__item" >
                  <label htmlFor="description">Omschrijving</label>
                  <textarea value={description} onChange={handleDescriptionChange} id="description" name="description" cols={35} rows={5} />
              </section>
              <section className="form__item" >
              <label ><span className="alert">*</span> Gekoppelde cursus:</label>
              <AdminCoursesSelect includeList={ props.includeList } addToCourses={handleCourses} />
              </section>
              <section  >
                  <button className="btn important_btn"  >Nieuwe groep aanmaken</button>
                  <input name="userid" value={ user.id } type="hidden" />
              </section>
          </form>
          <section className="message-panel"></section>
      </article>
    </section>
  )
}

export default NewGroup;