import React, { Fragment} from 'react';
import UsersList from'./UsersList';
import NewUserForm from'./includes/NewUserForm';

function Users(props) {
  return( 
    <Fragment>
      <input type="checkbox" className="invi-select" id="db2" name="group_select" defaultChecked ></input>
      <label htmlFor="db2" className="group__label">
        <header className="dashboard-item-header"><h3><span className="blob">2</span> Gebruikers</h3></header>
      </label>
      <section className="dashboard__description"><p>Voeg hier gebruikers toe aan groepen..</p>
      </section>
      <article className="dashboard__item-columns">
        <NewUserForm checkout={ props.checkout }  registerUser={props.registerUser} groups={props.groups} />
        <UsersList
          // users={props.allUsers}
          groups={props.groups}
          showUserProfile={props.showUserProfile}
        />
      </article>
    </Fragment>
  )
}
export default Users;