import React, { useContext, useEffect } from 'react';
import CoursesContext from '../../context/courses/coursesContext';

import Spinner from '../layout/Spinner';


const AdminCoursesSelect = props => {

  const coursesContext = useContext(CoursesContext);
  const { courses, getCourses } = coursesContext;

  useEffect(() => {
    if (!courses) {
      
      getCourses();
    }
    // eslint-disable-next-line
  }, [ courses]);

  const handleChange = e => {
    props.addToCourses(e.target.value)
  }


  if( courses && courses.length > 0 ){
    return (
      <React.Fragment>
        <section className="courses-list"  >
          <ol>
          { courses.map( item => (
            <li key={item.id} >
              <input type="checkbox" onChange={handleChange} key={ item.id } id={`check_${item.id}`} name="courseID" value={ item.id } className="checkbox-custom"/> <label className="checkbox-custom-label" htmlFor={`check_${item.id}`}>{item.name}</label>
            </li>
          ) ) }
          </ol>
        </section>
      </React.Fragment>
    )
  } else {
    return ( <div><Spinner /></div> )
  }
}

export default AdminCoursesSelect;