import React, { useContext } from 'react';
import MaterialIcon from 'material-icons-react';
import CoursesContext from '../../context/courses/coursesContext';
import ProgressContext from '../../context/progress/progressContext';
import Spinner from '../layout/Spinner';
// import parseSRT from 'parse-srt';

const VideoDetail = props => {
  const coursesContext = useContext(CoursesContext);
  const progressContext = useContext(ProgressContext);
  const { currentVideo } = coursesContext;
  const {  perc_viewed } = progressContext;
  const generateMeta = ( data ) =>{
   
    var html = /(<([^>]+)>)/gi;
    var d = data.meta.transcriptie.replace(html, "");
    var lines = d.split('\n');
    var output = '';
    lines.forEach((line , index) => {
      const range = line.split( ' --> ');
      if(  range[1] === undefined ){
          if( ! /^\d+$/.test(line)){
            output+= '<span class="subtitle-line">'+line+' </span>';
        } 
      }
    });
      return output; 
  }

  if(currentVideo ){

    return (
      <div>
        <header className="card-header">
          <h3>{ currentVideo.post.post_title }</h3>
        </header>
        <nav className="course__meta">
          <span className="course__detail-meta"><MaterialIcon icon="movie" size="18"/> {currentVideo.meta.minuten} min {currentVideo.meta.seconden} sec.</span>
          <span className="course__detail-meta"><MaterialIcon icon="done_outline" size="18" /><div className="progress-bar"><div className="bar" style={{width : perc_viewed+'%'}}></div></div></span>
        
        </nav>
        <section className="right-content"  dangerouslySetInnerHTML={{ __html: generateMeta(currentVideo) }}>
        </section>
        
      </div>
    )
  } else {
    return(
      <Spinner />
    )
  }
   
  
}
export default VideoDetail;