import React, { Component } from 'react';
import Vimeo from '@u-wave/react-vimeo';

class HotVideoPlayer extends Component  {
    render() {     
        return (
            <section id="theater" >
                <Vimeo
                    video={this.props.vimeoID}
                    id="stage"
                    autoplay
                    start={this.props.start}
                    onLoaded={this.props.handleOnLoaded}
                    onError={this.props.handleError}
                    onPlay={this.props.handlePlayerPlay}
                    onTimeUpdate={this.props.handlePlayerUpdate}
                    onEnd={this.props.handleEnd}
                    onSeeked={this.props.handleSeeked}
                    onPause={this.props.pauseThePlayer}
                />
            </section>
        )
    }
}

export default HotVideoPlayer;
