import React, { useContext  } from 'react';
import MaterialIcon from 'material-icons-react';
import UsersContext from '../../context/users/usersContext';
import ProgressContext from '../../context/progress/progressContext';
import CoursesContext from '../../context/courses/coursesContext';


import AddToGroup from './includes/AddToGroup';
// import SubscriptionsForm from './SubscriptionsForm';

import ActiveSubscriptionsRowItem from './ActiveSubscriptionsRowItem';
import ViewedList from './ViewedList';


const Modal = props => {

    const usersContext = useContext(UsersContext);
    const progressContext = useContext(ProgressContext);
    const coursesContext = useContext(CoursesContext);

    const { currentUser , deleteUserProfile ,removeUserFromGroup } = usersContext;
    const { courses } = coursesContext;
    const { cleanUpProgress } = progressContext;
    

    //Close the Modal
    const onClose = (e) => {
        e.preventDefault();
        cleanUpProgress();
        deleteUserProfile();
    }
    const onClick = e => {
        e.preventDefault();
        const confirmed = window.confirm("Hiermee wordt de gebruiker uit de groep gezet en de bijbehorende inschrijving op non-actief.\nDeze actie kan niet ongedaan worden gemaakt!\n\nWeet u zeker ");
        if (confirmed) {
            console.log("Confirmed: " + e.target.dataset.id);
            const data = {
                action: 'removeFromGroup',
                userid: currentUser.id,
                groupid :  e.target.dataset.id
            }
            removeUserFromGroup(data );
        } else {
            console.log("Not Confirmed"+e.target.dataset.id )
        }
    }

    // create an Icon
    const setIcon = role => {
        switch (role) {
            case "student":
                return <MaterialIcon icon="person_outline" color="white" title={role} />;
                
            case "docent":
                return <MaterialIcon icon="person" color='white' title={role} />;
              
            default:
                return <MaterialIcon icon="person" color='white' title={role} />;
        }
    }

    // Show the Modal when a User is selected 
    if (currentUser) {
        const {  display_name, role, email, subscriptions, groups } = currentUser;
        // const date = new Date(user_registered);
        // const registered = date.toLocaleString("nl-NL");
        let groupNames = [];
        if (groups.length > 0) {
            groups.forEach(element => { groupNames.push(<button className="btn btn-delete disabled" disabled data-id={element.group.group_id} key={element.group.group_id} title={`Verwijder gebruiker uit ${element.group.name}`} onClick={onClick}> {element.group.name} </button> )});
        }
       
        const list = subscriptions.map((subscription, index) => {
            const { subscriptionmeta, active } = subscription;
            const total = parseInt(subscriptionmeta.cursussen[0]);
            let coursesList = [];
            let list = [];
            for (let i = 0; i < total; i++) {
                const courseID = subscriptionmeta['cursussen_' + i + '_cursus_id'][0];
                courses.find((course) => { if (course.id === parseInt(courseID)) { coursesList.push(course) }; return null; });
            }
            list.push(<ActiveSubscriptionsRowItem key={index} factuurnummer={subscriptionmeta.factuurnummer} index={index} courses={coursesList} status={active} />);
            return list;
        });

        let emailDocent, inschrijvingenStudent;
        
        if (role === "docent") {
            emailDocent = <tr><td>Email Docent:</td><td>{email}</td></tr>;
        }
        if (role === "student") {
            inschrijvingenStudent = <tr><td>Inschrijvingen: </td><td>{list}</td></tr>;
        }
        // if (progress && progress.length > 0) { total = progress.length };
        return (
            <div className="modal user-modal" onClick={onClose}>
                <section className="modal-box" onClick={e => e.stopPropagation()}>
                    <header className="modal-header">
                        <span className="modal-header-grid">
                            { setIcon( role ) }
                            <h1>{currentUser.firstname} {currentUser.lastname}  <small> ( Geregistreerd als {display_name} )</small>
                            </h1>
                            <button onClick={onClose} className="btn low-action-btn"><MaterialIcon icon="close" color="white"/></button>
                        </span>
                    </header>
                    <article className="modal-main-content">
                        <table className="colofon">
                        <tbody>
                            <tr>
                                <td>Voornaam:</td>
                                <td>{currentUser.firstname}</td>
                            </tr>
                            <tr>
                                <td>Achternaam:</td>
                                <td>{currentUser.lastname}</td>
                            </tr>
                            <tr>
                                <td>Rol: </td>
                                <td>{currentUser.role}</td>
                            </tr>
                                { emailDocent }
                           
                            <tr>
                                <td>Lid van groep(en): </td>
                                <td>{groupNames}</td>
                            </tr>
                            { inschrijvingenStudent }
                            </tbody>
                        </table>
                      { role === 'student' ?  <ViewedList /> : <AddToGroup /> }
                    </article>
                    <footer>
                        <button className="btn btn-flat" onClick={onClose}>Sluit venster</button>
                    </footer>
                </section>
            </div>
        )
    } else {
        return null;
    }
    
}
export default Modal;