import React, { useContext } from 'react';
import VideoDetail from './VideoDetail';
import MaterialIcon from 'material-icons-react';
import CoursesContext from '../../context/courses/coursesContext';
import './player.css';
import Spinner from '../layout/Spinner';


const VideoFooter = props => {
  const coursesContext = useContext(CoursesContext);
  const { currentCourse } = coursesContext;
 
   
  if( currentCourse ){
    return (
      <footer className="video__footer">
        <section className="leftColumn">
          <header className="card-header">
            <h2>{currentCourse.name}</h2>
          </header>
          <section className="course__meta">
            <span className="course__detail-meta"><MaterialIcon icon="movie" size="18"/> {currentCourse.acf.aantal_videos} video's</span>
            <span className="course__detail-meta"> <MaterialIcon icon="timer" size="18" /> {currentCourse.acf.totale_tijdsduur}</span>
          </section>
          <article  dangerouslySetInnerHTML={{ __html: currentCourse.acf.info }}></article>
        </section>
        <section className="rightColumn">
        <VideoDetail />
        </section>
        
      </footer>
    )
  } else {
    return (
      <footer className="video__footer">
       <section className="leftColumn"><Spinner /></section>
       <section className="rightColumn"><Spinner /></section>
      </footer>
    )
  }

}

export default  VideoFooter;