import React, { useContext } from 'react'
import { Link } from 'react-router-dom';

import AuthContext from '../../context/auth/authContext';

const LoggedIn = props => {


  const authContext = useContext(AuthContext);
  const {  user, logOut } = authContext;

   const logOutHandler = ( e ) =>{
    logOut();
    e.currentTarget.value ="Cache geleegd, ververs nu de pagina"
    //window.location.reload();
  }

    return (
    
         <section className="form">
          <header className="card-header"><h2>Welkom terug, {user.name }</h2></header>
          <article className="loggedin-content">
          <h5>Je bent nu ingelogd</h5>
            <p>Ga verder naar onze <Link to='/'><button className="btn">Start pagina</button></Link></p>
            <h5>Wil je uitloggen?</h5>
            <p>Klik hieronder om uit te loggen</p>
            <button className="btn" onClick={ logOutHandler }>Log Out</button>
          </article>
        </section>
  
    )
}
export default LoggedIn;