import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';

if (document.getElementById('root')) {
  console.log("Start")
  let root = document.getElementById('root')
  let checkout = root.dataset.checkout;
  let portal = root.dataset.port;
  ReactDOM.render(<App checkout={ checkout } portal={portal}/>, document.getElementById('root'));
}

