import React ,{ useState , useContext ,useEffect } from 'react';
import AllListItem from './AllListItem';
import UsersFilter from './includes/UsersFilter';
import Pagination from '../administration/Pagination'; 
import UsersContext from '../../context/users/usersContext';
import { RESOURCE_URL, CHECKOUT } from '../../context/types';
import Spinner from '../layout/Spinner';


const UsersList = props => {
  
  const usersContext = useContext(UsersContext);

  const { users , groups, getAllUsers ,filtered, total, loading } = usersContext;

  const [isOpen, setIsOpen] = useState(true);


  const getNextPage = e => { 
    console.log(e);
    getAllUsers(100 , e);
    // getProgress(user.id, 10, e );
  }


  const controller = {
    total: total,
    limit: 100,
    offset : 0
  }


  useEffect(() => {
    getAllUsers();
    // eslint-disable-next-line
  },[])

  const getCourses = (ugroup) => {
    
    if(ugroup && ugroup.length>0 ){
       const grs = groups;
      let grp = [];
      if (grs) {   
        for( let i=0 ; i < ugroup.length ; i++ ) {
          if( i!== 0 ){
            const id = ugroup[i].group.group_id;
            grs.find( (data) =>{ if(data.id === id ){ grp.push( data.courses )}; return null; })
          }
        }
      }
      return grp;
    }
  }
  return (
    <section className="dashboard-item">
      <input type="checkbox" checked={ isOpen } onChange={ () => setIsOpen( !isOpen )} className="invi-select" id="ul1" name="users_select" ></input>
      <label htmlFor="ul1" className="group__label">
        <header className="dashboard-item-header"><h3>Geregistreerde Gebruikers {total}</h3></header>
      </label>
      <article className="dashboard-item-content group_table">
        <UsersFilter />
          <Pagination controller={ controller } getNewProgress={getNextPage}  onTop={ true } />
        <section className="group_table_scroll">
          { loading ? <Spinner /> : null }
          <table className="users_table"> 
          <thead>
            <tr>
              <th>Rol</th>
              <th>Gebruiker</th>
              <th>Inschrijving</th>
              <th>Groep(en):</th>
              <th>Actief</th>
            </tr> 
            </thead>
            <tbody>
              {
                filtered !== null ? filtered.map(
                  (user, index) => (<AllListItem user={user} key={index} courses={getCourses(user.groups)} />)) :
                users.map(
                  (user, index) => (<AllListItem user={user} key={index} courses={getCourses(user.groups)} />)
                )
              }
            </tbody>
            </table>
          </section>
          <Pagination controller={ controller } getNewProgress={getNextPage}/>
      </article>
      <aside>
        {/* <a href={ RESOURCE_URL +'/wp-admin/admin-ajax.php?action=csv_pull&nonce='+CHECKOUT+'' } className="btn download-link" >Download als Excel bestand.</a> */}
      </aside>
    </section>
  )
}
export default  UsersList;