import React, { useContext } from 'react';
import { Redirect } from 'react-router-dom';
import './Dashboard.css';
import ProgressList from './elements/ProgressList';

import SubscriptionsList from './elements/SubscriptionsList';
import TeacherSubscribedCourses from './elements/TeacherSubscribedCourses';
import TeacherCourses from './elements/TeacherCourses';

import Spinner from '../layout/Spinner';
import AuthContext from '../../context/auth/authContext';

// Dashboard
const Dashboard = props => {

  // Use Context
  const authContext = useContext(AuthContext);
  const { user  } = authContext;

  if( user ){
  
    const { id, first_name, last_name, roles, name } = user;
    if (roles === "instelling") {
       return <Redirect to='/administratie' />
    } else if (roles === "student") {
      document.title = `Dashboard van ${first_name} ${last_name}`;
    } else if (roles === 'docent') {
      document.title = `Dashboard van docent ${name} `;
    }
    
    return (
      <main className="container">
        <section className="dashboard">
          <header className="card-header" id={`user_${ id }`}><h2>Dashboard van {first_name} {last_name} </h2></header>
          <section className="dashboard__content">
            <article className="dashboard__content-panel">
              <header className="course__header"><h4>Recent bekeken:</h4></header>
              <section className="course__list_view">
                  <ProgressList />
              </section>
            </article>
            <article className="dashboard__content-panel">
              <header className="course__header"><h4>Uw cursussen:</h4></header>
                { user.roles === "student" ? <SubscriptionsList /> : <TeacherCourses /> }
            </article>
          </section>
        </section>
      </main>
    )
    } else {
    return( 
      <main className="container">
      <Spinner />
      </main>
    );
  }
}


export default Dashboard;
