import React, { Component } from 'react';
// import MaterialIcon from 'material-icons-react';
import { Link } from "react-router-dom";
import Image from '../layout/Image';

export default class DashboardCourseItem extends Component {


  render() {

    const course = this.props.course;
    
    if( course ) { 
      // console.log(course.slug)
      return (
        <Link to={`video/`+course.id+"/"+course.acf['video-link']} className="dashboard__course-link" title={`Ga naar ${course.name}`}>
        <article className="dashboard__grid-item" id={ course.id } >
        <header className="thumb-header"><h5>{ course.name}</h5> </header>
        <Image course={course } />
       
      </article>
      </Link>
      )
    } else {
      return ( <span>Laden...</span>)
    }
   
  }
}
