import React, { useContext, useState  } from 'react';

import UsersContext from '../../../context/users/usersContext';

function NewUserForm(props) {
   
  const usersContext = useContext(UsersContext);
  const { groups, registerUser } = usersContext;
  let showlist = [];

  const randomPassword = (length = 10 ) => {
    var chars = "ABCDEFGHIJKLMNOP1234567890";
    var pass = "";
    for (var x = 0; x < length; x++) {
        var i = Math.floor(Math.random() * chars.length);
        pass += chars.charAt(i);
    }
    return pass;
  }


  const [firstName, setFirstName] = useState('');
  const [lastName, setLastName] = useState('');
  const [code, setCode] = useState('');
  // const [role, setRole] = useState(null);
  const [email, setEmail] = useState('');
  const [userGroup, setUserGroup] = useState(0);
  const [checked, setChecked] = useState(false);
  const [password, setPassword] = useState(randomPassword);
  const [ hidden ,setHidden ] = useState(true);

  if (groups && groups.length > 0) {
    showlist = groups.map((item) => { return (<option key={item.id} value={item.id}>{item.group.name}</option>) });
  } else {
    return null;
  }


  const registerNewUser = (e) => {
    e.preventDefault();
    const formData = {
      'firstName': firstName,
      'lastName': lastName,
      'email': email,
      'password': password,
      'code': code,
      'role': checked,
      'group': userGroup,
      'action' : 'registerByCode'
    };
    registerUser(formData);
  }
  const setUserChecked = () => {
    if (checked) {
      setEmail('');
      setPassword('');

    }
    setChecked(!checked);
  }

  const onChangeHandler = e => {
    console.log(e.target.name, e.target.value)
    const val = e.target.value;
    switch (e.target.name) {
      case "firstname":
        setFirstName(val)
        return;
      case "lastname":
        setLastName(val);
        return;
      case "code":
        setCode(val)
        return;
      case "email":
        setEmail(val)
        return;
      case "role":
        setUserChecked()
        return;
      case "password":
        setPassword( val )
        return;
      case "group":
        setUserGroup(val);
        return;
      default:
        return;
    }
  
  }
//   const teacherPart = checked ?  ( return{  < section className = "form__item" >
//     <label htmlFor="email">Email: (<span className="alert">* </span>indien docent) </label>
//     <input id="email" name="email" onChange={onChangeHandler} value={email} type="email" placeholder="Voer hier de email van Docent in" />
//   </section >})
// : null;
  let teacherPart = "";
  if (checked) {
    teacherPart =
      <div className="dashboard-item">
        <header className="dashboard-item-header"><h3>Docent gegevens:</h3></header>
        <article className="dashboard-item-content">
          <section className="form__item" >
            <label htmlFor="email">Email: (<span className="alert">* </span>indien docent) </label>
            <input id="email" name="email" onChange={onChangeHandler} value={email} type="email" placeholder="Voer hier de email van Docent in" />
          </section >
          <section className="form__item" >
            <label htmlFor="password">Wachtwoord: (<span className="alert">* </span>indien docent) </label>
            <input id="password" name="password" onChange={onChangeHandler} value={password} type={ hidden ? "password" : "text"} placeholder="Voer hier een sterk wachtwoord in" />
          </section >
          
        </article>
      </div>;
  }

  return (
   <section className="dashboard-item">
      <header className="dashboard-item-header"><h3>Nieuwe Gebruiker</h3></header>
      <article className="dashboard-item-content">
        <form onSubmit={registerNewUser }>
          <section className="form__item">
            <label htmlFor="firstname"><span className="alert">* </span>Voornaam: </label>
            <input id="firstname" name="firstname" value={firstName} onChange={onChangeHandler} required type="text" placeholder="Voer hier de voornaam in" />
          </section>
          <section className="form__item">
          <label htmlFor="lastname"><span className="alert">* </span>Achternaam: </label>
            <input id="lastname" name="lastname" value={lastName} onChange={onChangeHandler}  required type="text" placeholder="voer hier de achternaam in" />
          </section>
          <section className="form__item">
          <label htmlFor="code"><span className="alert">* </span>Interne code: </label>
            <input id="code" name="code" required type="text" onChange={onChangeHandler}  value={code} placeholder="Voer hier de internecode in" />
          </section>
          <section className="form__item">
            <label htmlFor="role">Rol: </label>
            <h5>
              <input id="role" className="checkbox-custom" onChange={onChangeHandler} checked ={ checked }  name="role" type="checkbox" />
              <label htmlFor="role" className="checkbox-custom-label"> Docent</label>
            </h5>
          </section>
         {teacherPart}
          <section className="form__item">
            <label htmlFor="select1" ><span className="alert">* </span>Voeg toe aan een groep:</label>
            <select className="select-css" multiple={false} onChange={onChangeHandler}  value={userGroup} name="group" required >{showlist}</select>
          </section>
          <section >
            <button className="btn important_btn">Maak nieuwe gebruiker aan</button>
            <input type="hidden" name="action" value="registerByCode" />
          </section>
        </form>
      </article>
    </section>
  )
}

export default NewUserForm;
