import React, {  useContext } from 'react'
import UsersContext from '../../context/users/usersContext';
import MaterialIcon from 'material-icons-react';

const GroupsTableRow = props => {

  const usersContext = useContext(UsersContext); 
  const { getUserProfile } = usersContext;


  const clickHandler = ( e ) =>{
    e.preventDefault();
    getUserProfile( props.user );
    // console.log("Click", e.target.id)
  }
  const downloadHandler = ( e ) =>{
    e.preventDefault();
    props.downloadProgress( e.target.id);
    //  console.log("Click", e.target.id)
  }
 
    const {id , first_name, last_name, roles, subscriptions } = props.user; //#endregion
  const role = roles
  let subscription = null;
  let status = "1";
  if ( subscriptions && subscriptions.length > 0) {
    subscription = subscriptions[0];
    status = subscription.active;
  }
    let title = "Status: In afwachting";
    if( status === null || status==="0" ){
      status = 0;
    } else if( status === "1"){
      title = "Status: Actief";
    } else {
      title ="Status: Inactief"; 
    }
    const status_name = <span title={title} className={'state state_'+status}></span>;
    let icon ='';
    switch (role.toLowerCase()) {
      case "student":
      icon = <MaterialIcon icon="person_outline" color="#49b4aa" title={`${role}_${id}`} />
      break;
      case "docent":
      icon = <MaterialIcon icon="person" color='#2d89cc' title={role}/>
      break;
      default :
      icon = <MaterialIcon icon="person" color='#004474' title={role}/>
    }
    return (
      <tr className="user-list__item">
      <td className="user-list__icon">{icon }</td>
        <td className="user-list__name" >
          <div onClick={ clickHandler } id={ id} >{ first_name } { last_name }</div>
        </td>
        <td className="user-list__date" ><strong>{  subscription?subscription.subscription: 'N.v.t'}</strong></td>
        <td className="user-list__date" >{subscription?<MaterialIcon icon="archive" onClick={downloadHandler} id={id} /> : null}</td>
        <td className="user-list__date" >{status_name}</td>
      </tr>
    )
}
export default GroupsTableRow;