import React, { useContext ,useRef, useEffect } from 'react';
import UsersContext from '../../../context/users/usersContext';

const UsersFilter = () => {

  const usersContext = useContext(UsersContext);
  const { filterUsers, filterTeachers , clearFilter, filtered } = usersContext;
  const text = useRef('');
  const filterTeacher = useRef(false);

  useEffect(() => {
    if (filtered === null) {
      text.current.value = '';
    }
  })
  const onChange = e => {
   
    if (text.current.value !== '' ) {
     filterUsers( e.target.value)
    } else {
      clearFilter();
    }
  }
  const onFocus = e => {
    filterTeacher.current.checked = false;
    clearFilter();
  }

  const onChecked = e => {
    clearFilter();
    if (filterTeacher.current.checked) {
      filterTeachers(filterTeacher.current.checked);
    }
  }

  return (
    
    <section  className="filter-bar">
      <span>
        <input ref={text} type="text" id="filter-users" placeholder="Zoek gebruiker"  onFocus={onFocus} onChange={onChange} />
      </span>
      <span className="checkbox-filter">
        <input type="checkbox" name="filter-docent" className="checkbox-custom" id="filter-docent" ref={filterTeacher} onChange={onChecked}/>
        <label htmlFor="filter-docent" className="checkbox-custom-label">Alleen Docenten</label>
      </span>
    </section>
  )
}

export default UsersFilter
