import React , { useState } from 'react'
import Spinner from '../layout/Spinner';

const Pagination = props => {

  const [ index , setIndex ] = useState(0);
  
  const { total ,  limit  } = props.controller;
  const onTop = props.onTop;
  const onClick = ( e ) => {
    console.log(e.target.dataset.index)
    setIndex( e.target.dataset.index );
    props.getNewProgress( e.target.dataset.index );
  }
    if( limit < total ){
      const pages = Math.ceil(total / limit );
      let btns=[];
      let max = limit;
      if (  parseInt((index * limit ))+max  > total ) {
        max = total;
      } else {
        max = parseInt(index * limit) + max;
      }
     for( let i=0 ; i< pages; i++){
      let name= "btn";
       if( i === parseInt(index) ){
        name= "btn active";
       }

      btns.push(<li className={name} key={i} data-index ={i} onClick={ onClick }>{i+1}</li>);
     }
      return (
        <div className="viewedlist-nav">
          { onTop ?  <span className="small-nav">Toont { parseInt(index * limit )+1 } t/m { max  } van {total} </span> : null }
          <ol className="viewedlist-list">
          {btns}
          </ol>
         { onTop ? null : <span className="small-nav">Toont { parseInt(index * limit )+1 } t/m { max  } van {total} </span>}
        </div>
      )
    } else {
      return <Spinner />;
    }
 
}
export default Pagination;