import React, { useReducer  } from 'react';
import AuthReducer from './authReducer';
import AuthContext from './authContext';
import {
  USER_LOADED,
  LOGIN_URL,
  ADMIN_LOADED,
  NOT_LOGGED_IN,
  WAS_LOGGED_IN,
  REGISTER_FAIL,
  RESOURCE_URL,
  LOGOUT
} from '../types';

const AuthState = props => {
  
   //Set State
   const initialState = {
     session: JSON.parse(localStorage.getItem('session')),
     isAuthenticated: null,
     loading: false,
     error: null,
     user: null,
     subscriptions: null,
     currentSubscription : null
  }

  // Connect to Reducer
  const [state, dispatch] = useReducer(AuthReducer, initialState);


  // check Registration 

  const checkRegistration = async data => {

    const path = `${RESOURCE_URL}/wp-admin/admin-ajax.php`;
    try {
      const response = await fetch( path, { method: 'POST', body: data } );
      const jsonRes = await response.json();
      console.log(jsonRes.success);
      if ( jsonRes.success ) {
        dispatch({
          type: USER_LOADED,
          payload: jsonRes.data
        })
      } else {
        dispatch({ 
          type: REGISTER_FAIL , 
          payload : jsonRes.data
        })
      }
     
    } catch (error) {
      dispatch({ 
        type: REGISTER_FAIL , 
        payload : error
      })
    }

  }

  const checkedForErrors = data => {
    if (data.data) {
      return ( ( data.data.status && ( data.data.status === 403 || data.data.status === 404 ) ) ? false: true );
    }
    return true;    
  }

  //load User
  const loadAdmin = async data => {
    if (localStorage && localStorage.getItem('userData') && localStorage.getItem('session') ) {
      const userobject = JSON.parse((localStorage.getItem('userData')));
      const session = JSON.parse((localStorage.getItem('session')));
      const url = RESOURCE_URL + '/wp-json/wp/v2/users/' + userobject.user_id;
      try {
        const results = await fetch( url ,{ headers: { 'Content-Type': 'application/json; charset=utf-8', 'authorization': 'Bearer ' + session.token } } );
        const userRes = await results.json();        
        dispatch({ type: ADMIN_LOADED, payload: userRes });
      } catch (e) {
        console.log(e) 
      }
    } else {
      const res = await fetch(LOGIN_URL, { method: 'POST', body: data, });
      const jsonRes = await res.json();
      if (checkedForErrors(jsonRes)) {
        console.log('Token s Checked, continue...')
        try {
          const url = RESOURCE_URL + '/wp-json/wp/v2/users/' + jsonRes.user_id;
          const results = await fetch( url ,{ headers: { 'Content-Type': 'application/json; charset=utf-8', 'authorization': 'Bearer ' + jsonRes.token } } );
          const userRes = await results.json();  
          if (checkedForErrors(userRes)) {
            dispatch({ type: ADMIN_LOADED, payload: userRes });
            localStorage.setItem('session', JSON.stringify(jsonRes));
          } else {
            console.log(userRes);
            localStorage.clear();
            dispatch({
              type: REGISTER_FAIL,
              payload: userRes
            });
          }
        } catch (e) {
          console.log(e);
          localStorage.clear();
          dispatch({
            type: REGISTER_FAIL,
            payload: e
          });
        }
      } else {
        localStorage.clear();
        dispatch({
          type: REGISTER_FAIL,
          payload: jsonRes
        });
      }
    } 
  }
  
  // Logout
  const logOut = () => {
    dispatch({ type: LOGOUT });
  };
  // check for previous login
  const isLoggedIn = () => {
    if ( localStorage && localStorage.getItem('userData') && localStorage.getItem('session')  ) {
      dispatch({ type: WAS_LOGGED_IN  })
    } else {
      dispatch({ type: NOT_LOGGED_IN });
    }
    
  }

  // Clear Errors

  // Return State Component
  return (
    <AuthContext.Provider
      value={{
        //
        // state objects
        //
        session: state.session,
        isAuthenticated: state.isAuthenticated,
        loading: state.loading,
        subscriptions: state.subscriptions,
        user : state.user,
        error: state.error,
        //
        // functions
        //
        checkRegistration,
        isLoggedIn,
        logOut,
        loadAdmin
      }} >
    {props.children}
    </AuthContext.Provider>
  )

}
export default AuthState;